<template>
  <div class="center" :style="htmlheight">
    <div class="center-title" >
      <p class="grzx">个人中心</p>
      <div class="tx">
        <img src="../assets/tx.png" alt="">
      </div>
      <p style="color:rgb(27, 74, 119);">用户名</p>
    </div>

    <div class="content">
      <div class="mycenter">
        <div class="imgp">
          <img src="../assets/center.png" alt="">
          <p>个人中心</p>
        </div>
        <p>></p>
      </div>
      <div class="question">
        <div class="imgp">
          <img src="../assets/question.png" alt="">
          <p>常见问题</p>
        </div>
        <p>></p>
      </div>
      <div class="about">
        <div class="imgp">
          <img src="../assets/about.png" alt="">
          <p>关于我们</p>
        </div>
        <p>></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
        htmlheight:0
    }
  },
  mounted(){
    const htmlh=window.screen.height;
    this.htmlheight="height:"+window.screen.height+"px"
    // console.log(window.screen.height ) 
  }
}
</script>

<style scoped>
  .center-title{
    width: 100%;
    height: 25%;
    background: #d6ece5;
  }
  .tx>img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
  }
  .grzx{
    display: inline-block;
    margin: 0 auto;
    margin-top: 5%;
    font-size: 18px;
    color: rgb(27, 74, 119);
    font-weight: 700;
  }
  .mycenter,.question,.about{
    width: 90%;
    height: 33px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #c9c9c9;
    margin-top: 7%;
  }
  .imgp>img{
    width: 24px;
    height: 24px;
    margin-right: 5%;
  }
  .imgp{
    width: 35%;
    display: flex;
  }
</style>


