<template>
		<div id="all">
				<div id="input">
					<div id="username">
						<p><span class="span-font">姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名：</span></p>
						<input type="text" class="input-size" placeholder="bjmlby@163.com" />
					</div>
				<div class="sex">
						<p><span class="span-font">性&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;别：</span></p>
						<select class="input-sex" v-model="selectCurriculums">
      			<option v-for="(curriculum, index) in curriculums" :key="index">{{curriculum}}</option>
    </select>
				</div>
					<div id="danwei">
						<p><span class="span-font">单&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;位：</span></p>
						<input type="text" class="input-size" placeholder="北京茂澜宝业" />
					</div>
					<div id="phone">
						<p><span class="span-font">手&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;机：</span></p>
						<input type="text" class="input-size" placeholder="18801230123
" />
					</div>
					<div id="email">
						 <p><span class="span-font">住&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;址：</span></p>
						 <input type="text" class="input-size" placeholder="北京市海淀区" />
					</div>
					<div id="louhao">
						<p><span class="span-font">楼&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号：</span></p>
						<input type="text" class="input-size" placeholder="18" />
				</div>
					<div id="menpaihao">
						<p><span class="span-font">门&nbsp;牌&nbsp;&nbsp;号：</span></p>
						<input type="text" class="input-size" placeholder="301">
					</div>
					<div id="email">
						<p><span class="span-font">电子邮件：</span></p>
						<input type="text" class="input-size" placeholder="1282018981@qq.com">
					</div>
			<!-- 提交按钮 -->
			<div id="button">
				<input type="button"  id="submit-button" value="加入我们" @click="jumpLogin()" style="color: #fff;"/>
				<!-- onclick="javascript:window.location.href='https://www.baidu.com'" -->
			</div>
			</div>
		</div>
</template>
<script>
export default {
	data(){
		return{
			selectCurriculums: '男',
      curriculums: ['男', '女']
		}
	},
	methods:{
		jumpLogin(){
			// this.$router.push('https://www.baidu.com')
		}
	}
}
</script>
<style scoped>
		*{
			margin:0;padding: 0;
		}
		#all{
			width: 90%;
			height: 100%;
			margin: 0 auto;
			position: relative;
		}
		#input{
			width: 100%;
			position: absolute;
			top: 50%;
			transform: translateY(6%)
		}
		#input>div>p{
			display: inline-block;
		}
		#input>div{
			margin-bottom: 6%;
		}
		#username{
			margin-top: 10%;
		}
		.span-font{
			font-size: 14px;
		}
		.input-size{
			width: 70%;
			height: 25px;
			border-radius: 5px;
			border: 1px solid #333;
			padding-left:3px !important;
			outline: none;
		}
		#submit-button{
			display: block;
			width: 140px;
			margin: 0 auto;
			margin-top: 20%;
		}
		#submit-button{
			align-self:center;
			padding:2px 2px 2px 2px;
			box-sizing:border-box;
			background-color:rgba(22, 155, 213, 1);
			width:140px;
			height:40px;
			border:none;
			border-radius:5px;
			-moz-box-shadow:none;
			-webkit-box-shadow:none;
			box-shadow:none;
		}
		.input-sex{
			width: 70%;
			height: 27px;
			border-radius: 5px;
			border: 1px solid #333;
			padding-left:3px;
			outline: none;
			box-sizing: border-box;
		}
</style>


