<template>
  <div>
    <span>用户名</span>
    <input type="text" v-model="username" >
    <span>密码</span>
    <input type="password" v-model="userpass">
    <button @click="log()">登录</button>
  </div>
</template>

<script>
export default {
  data(){
    return{
      username:"",
      userpass:""
    }
  },
  methods:{
    log(){
       var url = `user/login.do?username=${this.username}&&password=${this.userpass}`;
       this.$axios.post(url).then(res=>{
          console.log(res.data.data)
       })
    },
  }
}
</script>

<style scoped>

</style>


