<template>
  <div>
    <p class="title-p">立即报修</p>
    <div class="bxDetails">
      <mt-field class="field" label="报修人姓名:" placeholder="Input username" v-model="username"></mt-field>
      <div class="block">
        <span class="demonstration">预约时间</span>
      <el-date-picker v-model="value1" type="datetime" placeholder="选择日期时间">
      </el-date-picker>
      </div>
      <mt-field class="field" label="报修人电话：" placeholder="Input tel" type="tel" v-model="phone"></mt-field>
      <div class="field" style="height:60px;padding:0 10px;margin-top:2%;display:flex;position:relative;border-bottom:0px">
        <span style="width: 100px;display: block;height: 20px;">报修人地址:</span>
        <input readonly="readonly" placeholder="北京市海淀区莲花池西路16号金鑫大厦
" style="border:transparent;outline:none;width:80%;position:absolute;top:50%;"/>
      </div>
        <!-- 选择地图 -->
    <div class="map">
      <div class="weizhi">
        <img src="../assets/weizhi.png" alt="">
        <p>选择地址</p>
      </div>
    </div>
    <!--  -->
    </div>
    <!-- 故障描述 -->
    <div class="repairDetail">
      <p style="color:#3498db;font-weight:700;width:25%;display: block;text-align:left">故障描述</p>
        <textarea  class="upload-font" cols="30" rows="1" placeholder="输入问题描述,上传语音或图片"></textarea>
    <!-- 上传语音 -->
        <div class="yx"><button class="yuyin">上传语音</button></div>
    <!-- 上传图片 -->
        <div class="uploadImg">
          <el-upload action="#" list-type="picture-card" :auto-upload="false" :limit="3"  accept=".jpg,.jpeg,.png,.gif,.bmp,.pdf,.JPG,.JPEG,.PBG,.GIF,.BMP,.PDF">
              <i slot="default" class="el-icon-plus"></i>
              <div slot="file" slot-scope="{file}">
                <img class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                <span class="el-upload-list__item-actions">
                  <span class="el-upload-list__item-preview" 
                  @click="handlePictureCardPreview(file)">
                    <i class="el-icon-zoom-in"></i>
                  </span>
                  <span v-if="!disabled" class="el-upload-list__item-delete"                @click="handleDownload(file)">
                    <i class="el-icon-download"></i>
                  </span>
                  <span v-if="!disabled" class="el-upload-list__item-delete"
                    @click="handleRemove(file)">
                    <i class="el-icon-delete"></i>
                  </span>
                </span>
              </div>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
   </div>
   <div class="tijiao"><button>提交</button></div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      username:'',
      birthday:'',
      phone:'',
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false,
        pickerOptions: {
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
        value3: ''
    }
  },
  methods:{
    user(){
      if(username==""){
        console.lig(1)
      }
    },
     handleRemove(file) {
        console.log(file);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      handleDownload(file) {
        console.log(file);
      }
    }
  }
</script>

<style lang="">
  .mint-cell-wrapper{
    background-size: 120% 0px !important; 
  }
  .el-upload-list__item-actions{
    width: 50px !important;
    height: 50px !important;
  }
  .el-upload el-upload--picture-card{
    width: 30px !important;
    height: 30px !important;
  }
</style>
<style scoped>
  *{
    box-sizing: border-box;
  }
  .title-p{
    display: inline-block;
    margin: 0 auto;
    margin-top: 5%;
    font-size: 18px;
    color: rgb(27, 74, 119);
    font-weight: 700;
    margin-bottom: 5%;
  }
  .field{
    width: 90%;
    margin: 0 auto;
    text-align: left;
    border-bottom:1px solid #DFDFDF;
  }
  .map{
    width: 100%;
    height: 40px;
    background:#eff2fd;
  }
  .weizhi{
    display: flex;
    width: 30%;
    float: right;
    margin-top: 1.5%;
  }
  .weizhi>p{
    color: #3498db;
  }
  .weizhi>img{
    width: 24px;
    height: 24px;
  }
  .repairDetail{
    width: 90%;
    margin: 0 auto;
  }
  .upload-font{
    width: 100%;
    height: 60px;
    background: #Eff3f2;
    border-radius: 10px;
    padding: 2%;
    resize:none;
    border: transparent;
    outline: none;
    font-size: 16px;
  }
  .upload-font::placeholder{
    color: #3498db;
  }
  .yx{
    width: 30%;
  }
  .yuyin{
    width: 100%;
    height: 30px;
    background-color: #ffaf00;
    border:transparent;
    color:#fff;
    border-radius: 3px;
    outline: none;
    margin-bottom: 5%;
  }
  .uploadImg{
    margin-bottom: 3%;
  }
  .tijiao>button{
    width: 45%;
    height: 35px;
    background: #30c9f7;
    border: transparent;
    color: white;
    outline: none;
    border-radius: 3px;
  }
</style>


