import Vue from 'vue'
import Router from 'vue-router'
import joinMe from '@/components/joinMe.vue'
import index from '@/components/index.vue'
import myCenter from '@/components/myCenter.vue'
import repairs from '@/components/repairs.vue'
import bmap from '@/components/bmap.vue'
import query from '@/components/query.vue'
import login from '@/components/login.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name:'index',
      component:index,
      title:'在线保修'
    },
    {
      path: '/joinMe',
      name: 'joinMe',
      component: joinMe
    },
    {
      path:'/myCenter',
      name:'myCenter',
      component:myCenter,
      meta: {
        title: 'shouy'
      }
    },
    {
      path:'/repairs',
      name:'repairs',
      component:repairs
    },
    {
      path:'/bmap',
      name:'bmap',
      component:bmap
    },
    {
      path:'/query',
      name:'query',
      component:query
    },
    {
      path:'/login',
      name:'query',
      component:login
    }
  ]
})



